.subscribeRow{
    display: flex;
    border-top: 0;
    border-bottom: 1px;
    border-right: 0;
    border-left: 0;
    border-style: solid;
    border-color: var(--button_bot_shadow);
    margin-top: 5px;
    background-color: var(--background_content);
    padding: 12px;
    color: var(--accent);
}
.subscribeIconExtender{
    display: flex;
    float: left;
    align-items: center;
    justify-content: center;
    width: 25px;
}
.subscribeRowFullContent{
    display:flex;
    width:100%;
    justify-content:space-around;
}
.subscribeRowContent{
    display: flex;
    max-width: 630px;
    flex-wrap: wrap;
    justify-content : space-around;
}
.subscribeRowTitleNameContainer{
    display: flex;
    float: left;
    align-items: center;
    justify-content: left;
    margin-left: 7px;
    width: 310px;
}
.subscribeRowButtonContainer{
    display: flex;
    margin-top: 5px;
    align-items: center;
}
.subscribeRowButton{
    min-width: 149px;
    margin-left: 6px;
    height: 30px;
}
.fullTitleInformation{
    background-color: var(--background_light);
    border-top: 0;
    border-bottom: 1px;
    border-right: 0;
    border-left: 0;
    border-style: solid;
    border-color: var(--button_bot_shadow);
    justify-content : space-around;
}
.fullTitleInformationContainer{
    display:flex;
    max-width: 630px;
}
.fullTitleInformationImage{
    display:flex;
    float:left;
    width:160px;
    padding-top: 10px;
    padding-bottom: 10px;
}
